	let giftModal = uni.createAnimation({
		duration: 300,
		timingFunction: "ease",
		delay: 0
	})
	import rewardList from '@/components/reward-modal.vue';
	export default {
		components: {
			rewardList
		},
		data() {
			return {
				showReward: false,
				modalBar: [{
					name: '礼物',
					active: true
				}, {
					name: '我的道具',
					active: false
				}],
				dataList:[],
				animationData: {},
				usename: 'xp'
			}
		},
		onLoad(options) {
			this.articleId = options.articleId
		},
		onShow() {
             this.initData()
		},
		methods: {
			initData() {
				let self = this
				let params = {
					articleId: this.articleId,
					pageIndex: 1,
					pageSize: 100
				}
				this.$acFrame.HttpService.rewardList(params).then(res=>{
					if(res.success){
						self.dataList=res.data?res.data:[]
					}
				})
			},
			rewardPeople() {
				this.showReward = true
			},
			hideModal() {
				this.showReward = false
			},
			chooseItem(ind) {
				console.log(ind)
			},
			rewardInit() {
				this.showmask = false
			}
		}
	}
